#booth
    min-height: calc(100vh - 16px)
    background: url('/images/bg-booth.png') #FFFFFF
    background-repeat: no-repeat
    background-position: 0% 0%
    background-size: 100% auto
    margin-top: -20px
    padding-top: 50px
    @include screen('lg')
        margin-top: -70px
        padding-top: 120px
    .header
        background: radial-gradient(50% 983.68% at 50% 50%, #FDC12D 0%, #D49B48 100%)
        border-radius: 30px
        width: fit-content
        padding: 1rem 4rem
        text-align: center
        margin: 1rem auto
        h1
            color: white
            font-style: normal
            font-weight: 600
            font-size: 22px
            span
                font-size: 20px
                display: block
                color: #313131
                font-weight: 400
                @include screen('lg')
                    font-size: 30px
            @include screen('lg')
                font-size: 31px
    #booth-list
        padding: 2rem 0
        div
            text-align: center
            display: flex
            align-items: flex-end
            justify-content: center
    #posters
        cursor: pointer
        display: inline !important
    #booth-item1,
    #booth-item2,
    #booth-item3,
    #booth-item4
        position: relative
        .btn
            position: absolute
            background: radial-gradient(50% 983.68% at 50% 50%, #b1b4af 0%, #484d46 100%)
            border: solid 2px white
            color: white
            border-radius: 45px
            transition: all 0.4s ease
            &:hover
                transform: scale(1.1)
            font-size: 0.6rem
            padding: 0.25rem 0.45rem
            @include screen('md')
                border: solid 3px white
                font-size: 1.25rem
                padding: .375rem 0.75rem

            svg
                animation: wobble 2s ease 0s infinite normal forwards
                width: 9px
                height: 9px
                @include screen('sm')
                    width: 16px
                    height: 16px
                @include screen('md')
                    width: 16px
                    height: 16px
                @include screen('lg')
                    width: 16px
                    height: 16px
    #booth-item1
        #video
            top: 40%
            left: 14%
            @include screen('sm')
                top: 40%
                left: 14%
            @include screen('md')
                top: 40%
                left: 14%
            @include screen('lg')
                top: 40%
                left: 14%
        #course
            top: 61%
            @include screen('sm')
                top: 61%
            @include screen('md')
                top: 62%
            @include screen('lg')
                top: 62%
        #posters
            top: 40%
            right: 12%
            @include screen('sm')
                top: 41%
                right: 12%
            @include screen('md')
                top: 42%
                right: 12%
            @include screen('lg')
                top: 45%
                right: 12%

    #booth-item2
        #video
            top: 45%
            right: 20%
            @include screen('sm')
                top: 45%
                right: 20%
            @include screen('md')
                top: 45%
                right: 20%
            @include screen('lg')
                top: 45%
                right: 20%
        #course
            top: 70%
            @include screen('sm')
                top: 72%
            @include screen('md')
                top: 72%
            @include screen('lg')
                top: 72%
        #posters
            top: 45%
            left: 18%
            @include screen('sm')
                top: 61%
                left: 17%
            @include screen('md')
                top: 45%
                left: 17%
            @include screen('lg')
                top: 45%
                left: 22%

    #booth-item3
        #video
            top: 38%
            left: 43%
            @include screen('lg')
                top: 43%
                left: 46%
        #course
            top: 68%
            left: 14%
            @include screen('lg')
                top: 70%
                left: 15%
        #posters
            top: 62%
            right: 27%
            @include screen('lg')
                top: 65%
                right: 30%

    #booth-item4
        #video
            top: 57%
            left: 78%
            @include screen('lg')
                top: 57%
                left: 67%
        #course
            top: 23%
            left: 52%
            @include screen('sm')
                top: 24%
                left: 52%
            @include screen('lg')
                top: 25%
                left: 55%
        #posters
            top: 34%
            right: 71%
            @include screen('sm')
                top: 34%
                right: 72%
            @include screen('lg')
                top: 35%
                right: 76%

    .header-booth-detail
        text-align: center
        h1
            font-weight: 600
            font-size: 34px
            color: #C97A1B
            position: relative
            @include stroke(2, #fff)
        nav
            margin-top: -28px
            display: flex
            justify-content: center
            a
                text-decoration: none
                color: white
                border: 3px solid #FFFFFF
                padding: 18px 24px
                h2
                    margin: 0
                    font-weight: 500
                    font-size: 18px
                    line-height: 27px
                &.border-right
                    border-radius: 131px !important
                &:last-child
                    background: #DBDBDB
                    border-radius: 0px 131px 131px 0px
                &:first-child
                    background: linear-gradient(264.78deg, #628D00 51.9%, #C7FA52 120.27%)
                    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25)
                    border-radius: 131px 0 0 131px




